@import 'styles/base/variables';
@import 'styles/base/grid';
@import 'styles/elements/scroll-bar';

.product-list {
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(100%, 1fr));
  width: 100%;
}

@include tablet() {
  .product-list{
    grid-template-columns: repeat(auto-fill,minmax(500px, 1fr));
  }
}