.button-primary {
  background-color: $primary;
  border: none;
  border-radius: 4px;
  color: $grey-lighter;
  cursor: pointer;
  font-size: 1rem;
  padding: .5rem 1rem;
  &:hover {
    background-color: $secondary; 
  }
  &:focus {
    background-color: rgba($secondary, 0.9);
  }
}
